<template>
  <card>
    <img slot="card-image" :src="article.cover_image"/>
    <template slot="header">
      <a class="text-primary-light" :href="article.url">{{ article.title }}</a>
    </template>

    <template slot="footer">
      <section class="flex justify-end">
        <span class="align-middle mr-3">
          <img style="height: 0.8rem;" :src="heartImg" />
          {{ article.positive_reactions_count }}
        </span>

        <span class="align-middle">
          <img style="height: 0.8rem;" :src="commentImg" />
          {{ article.comments_count }}
        </span>
      </section>

      <section class="flex flex-wrap justify-end text-xs font-thin">
        <span class="mt-1">Tags:</span>
        <span v-for="tag in article.tag_list"
          :key="tag"
          class="bg-secondary rounded mt-1 mx-1"
        >
          #{{ tag }}
        </span>
      </section>
    </template>
  </card>
</template>

<script>
import Card from './Card'

export default{
  name: 'BlogPostCard',
  components: { Card },
  props: [ 'article' ],
  computed: {
    commentImg() {
      return "https://practicaldev-herokuapp-com.freetls.fastly.net/assets/comments-bubble-7448082accd39cfe9db9b977f38fa6e8f8d26dc43e142c5d160400d6f952ee47.png";
    },

    heartImg() {
      return "https://practicaldev-herokuapp-com.freetls.fastly.net/assets/reactions-stack-4bb9c1e4b3e71b7aa135d6f9a5ef29a6494141da882edd4fa971a77abe13dbe7.png";
    }
  }
}
</script>