<template>
  <section class="py-8 px-8 bg-primary-light shadow-inner">
    <h1 class="text-center font-heading mb-4"> My Blog Posts </h1>
    <section class="flex flex-wrap justify-center">
      <p class="leading-normal text-justify lg:w-1/3">
        I occasionally write on <a class="text-secondary" href="https://dev.to">dev.to</a>, 
        a great community of developers. 
        I mostly write about my personal thoughts on development and productivity in general,
        or on the specifics of VueJS. Here are a few of my most recent posts.
      </p>
      <section class="flex flex-row flex-wrap lg:w-2/3 md:w-full justify-center">
        <blog-post-card
          v-for="article in articles"
          :key="article.id"
          :article="article"
          class="lg:w-1/4 w-1/2"
        />
      </section>
    </section>

    <footer>
      <a class="text-secondary" href="https://dev.to/mccabiles">See more >></a>
    </footer>
  </section>
</template>

<script>
import BlogPostCard from './BlogPostCard'

export default {
  name: 'BlogPosts',
  components: { BlogPostCard },
  data() {
    return {
      articles: [],
    };
  },

  mounted() {
    fetch('https://dev.to/api/articles?username=mccabiles&limit=3')
    .then(response => response.json())
    .then(articles => this.articles = articles);
  }
}
</script>